import React, { useEffect } from "react";
import { useState } from "react";
import StatisticItem from "../Statistics/StatisticItem";

import {
  dataAmount,
  priceIncreaseData,
  dataCityFake,
} from "../../datafake/DataFake";

const StatisticsPage = () => {
  const [amountData, setAmountData] = useState([]);
  const [priceData, setPriceData] = useState([]);

  useEffect(() => {
    setAmountData(dataAmount);
  }, []);
  useEffect(() => {
    setPriceData(priceIncreaseData);
  }, []);

  return (
    <div className="max-w-5xl md:mx-auto mx-2">
      <StatisticItem
        type="bar"
        dataAmount={amountData}
        title="Thng k s lng bi ng theo khu vc"
        desc="(nh gi  nng ng ca th trng theo tng khu vc c th)"
        bgColor="#1186C9"
        dataCityFake={dataCityFake}
      />
      <StatisticItem
        type="bar"
        dataAmount={amountData}
        title="Thng k trung bnh theo khu vc"
        desc="(nh gi s chnh lch gi ca th trng theo tng khu vc c th)"
        bgColor="#FA8701"
        dataCityFake={dataCityFake}
      />
      <StatisticItem
        type="line"
        priceData={priceData}
        title="Mc  tng gi theo giai on ca khu vc"
        desc="(nh gi  bin ng ca th trng theo tng khu vc c th)"
        bgColor="#FB6E80"
        dataCityFake={dataCityFake}
      />
    </div>
  );
};

export default StatisticsPage;
